import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import logo from "assets/img/orbitNewLogo2.png";
import { LogInHelper } from "app/logInHelper";
import dashboardRoutes from "routes/dashboard.jsx";
import { isAppReady } from 'app/adminHelper'
import { getPrimaryColor } from 'app/adminHelper'
import { getVersion } from '../../app/adminHelper'


class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      appIsReady: false
    };
  }
  componentDidMount = () => {
    this.timer = setInterval(this.checkIfAppIsReady, 1000);
    this.checkIfAppIsReady();
  }

  componentWillUnmount = () => {
    clearInterval(this.timer);
  }

  checkIfAppIsReady = () => {
    if (isAppReady()) {
      this.setState({ appIsReady: true });
      clearInterval(this.timer);
    }
  }

  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }
  updateDimensions() {
    this.setState({ width: window.innerWidth });
  }

  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }

  render() {
    //const sidebarBackground = {
    //  backgroundImage: "url(" + imagine + ")"
    //};
    let logInHelper = new LogInHelper();
    let isAdmin = logInHelper.isAdmin();
    let logInUser = logInHelper.getLogedInUser()
    return (
      <div
        id="sidebar"
        className="sidebar"
        data-color={'blue'}
      >
        <div className="sidebar-background" />
        <div className="logo" style={{ backgroundColor: 'white' }}>
          <center>
            <a
              href="dashboard"
              className="simple-text logo-mini"
            >
              {/* rotating logo
            <div className="logo-img">
              <img src={logo} alt="logo_image" style={{ animation: 'spin 5s linear infinite' }} />
            </div>
          */}
              <div className="logo-img">
                <img src={logo} alt="logo_image" style={{ height: '130%', width: '130%' }} />
              </div>
            </a>
            <a
              href="dashboard"
              className="simple-text logo-normal"
              style={{ color: getPrimaryColor() }}
            >
              ORBIT
          </a>
          </center>
        </div>
        <div className="sidebar-wrapper">
          <ul className="nav">
            {dashboardRoutes.map((prop, key) => {
              let show = true;
              if ((prop.show && prop.show === 'admin' && !isAdmin) ||
                (prop.redirect && prop.redirect === true)) {
                show = false;
              }
              if (show)
                return (
                  <li
                    className={
                      prop.upgrade
                        ? "active active-pro"
                        : this.activeRoute(prop.path)
                    }
                    key={key}
                  >
                    <NavLink
                      to={prop.path}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <i className={prop.icon} style={{
                        display: 'block',
                        marginLeft: 'auto',
                        marginRight: 'auto',
                        width: '100%'
                      }} />
                      <div>
                        <center><h4>{prop.name}</h4></center>
                      </div>
                    </NavLink>
                  </li>
                );
              return null;
            })}
            <center>
              <hr/>
              <div style={{ color: 'white' }}>{getVersion()}</div>
              <br />
              <div style={{ color: 'white' }}>User Name - {logInUser}</div>
              <hr/>
            </center>
          </ul>
        </div>
      </div>
    );
  }
}

export default Sidebar;
